/* eslint-disable jsx-a11y/anchor-is-valid */
import React, {FC, useEffect, useState} from 'react'
import {Link, withRouter} from 'react-router-dom'
import {Skeleton, Row, Col, Input, Select as Select1, Button, Spin, DatePicker} from 'antd'
import {KTSVG} from '../../../_metronic/helpers'
import moment from 'moment'
import 'react-datepicker/dist/react-datepicker.css'
import {mainConfig} from '../../../config/mainConfig'
import ShowMore from 'react-show-more'

const {RangePicker} = DatePicker

let token = ''

const RecentPo=(props: any)=> {
  const [loading, setloading] = useState<any>(false)
  const [loadingPOItems, setloadingPOItems] = useState<any>(false)
  const [AllPOs, setAllPOs] = useState<any[]>([])
  const [AllPOsTable, setAllPOsTable] = useState<any[]>([])
  const [AllPOHistorical, setAllPOHistorical] = useState<any[]>([])
  const [tabSelected, settabSelected] = useState<any>('Latest')

  const [AllPOSelected, setAllPOSelected] = useState<any[]>([])
  const [AllPOSelectedHistorical, setAllPOSelectedHistorical] = useState<any[]>([])
  const [AllPOItem, setAllPOItem] = useState<any[]>([])
  const [AllPOItemTable, setAllPOItemTable] = useState<any[]>([])
  const [AllPOItemH, setAllPOItemH] = useState<any[]>([])
  const [AllPOItemTableH, setAllPOItemTableH] = useState<any[]>([])
  const [PONumberSelected, setPONumberSelected] = useState<any>('')
  const [PONumberSelectedH, setPONumberSelectedH] = useState<any>('')

  const [FilterDate, setFilterDate] = useState<any>('')
  const [iscreateP, setIscreateP] = useState<any>('')
  const [spcode, setSpCode] = useState<any>('')
  const [spid, setSpid] = useState<any>('')
  const [POsortPN, setPOsortPN] = useState<any>('PN')
  const [POsortPNflag, setPOsortPNflag] = useState<any>('ASC')
  const [POsortRA, setPOsortRA] = useState<any>('RA')
  const [POsortRAflag, setPOsortRAflag] = useState<any>('ASC')
  const [POsortSC, setPOsortSC] = useState<any>('SC')
  const [POsortSCflag, setPOsortSCflag] = useState<any>('ASC')
  const [POsortSTATUS, setPOsortSTATUS] = useState<any>('STATUS')
  const [POsortSTATUSflag, setPOsortSTATUSflag] = useState<any>('ASC')

  useEffect(() => {
    getAllPO()
    setAllPOSelected([])
    setAllPOSelectedHistorical([])
    settabSelected('Latest')
  }, [])
  const getAllPO=()=> {
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/recent-pos`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status != 200) {
            setloading(false)
            localStorage.clear()
            window.location.reload()
          }
          response.json().then(function (data) {
            setAllPOs(data.data)
            setAllPOsTable(data.data)
            setloading(false)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {
      setloading(false)
    }
  }

  const onSearchPO=(e: any)=> {
    let n = e.target.value
    let searchdata = new Array()
    if (tabSelected == 'Latest') {
      AllPOs.map((i) => {
        if (
          i.po_num.toLowerCase().includes(n.toLowerCase()) ||
          i.created_by.toLowerCase().includes(n.toLowerCase()) ||
          (i.code !== null ? i.code.toLowerCase().includes(n.toLowerCase()) : '')
        ) {
          searchdata.push(i)
        }
      })
      setAllPOs(searchdata)
      if (n == '') {
        setAllPOs(AllPOsTable)
      }
    }
  }

  const onSortpoTable=(data: any)=> {
    let temp = tabSelected == 'Historical' ? AllPOHistorical : AllPOs
    let temp2 = tabSelected == 'Historical' ? AllPOHistorical : AllPOs

    if (data == 'PN') {
      if (POsortPNflag == 'Asc') {
        setPOsortPNflag('Desc')

        let tempArr = temp.sort((a: any, b: any) => {
          let fa = a.po_num.toLowerCase(),
            fb = b.po_num.toLowerCase()

          if (fa > fb) {
            return -1
          }
          if (fa < fb) {
            return 1
          }
          return 0
        })
        tabSelected == 'current' ? setAllPOs(tempArr) : setAllPOHistorical(tempArr)
      } else {
        setPOsortPNflag('Asc')
        let tempArr = temp.sort((a: any, b: any) => {
          let fa = a.po_num.toLowerCase(),
            fb = b.po_num.toLowerCase()

          if (fb > fa) {
            return -1
          }
          if (fb < fa) {
            return 1
          }
          return 0
        })
        tabSelected == 'current' ? setAllPOs(tempArr) : setAllPOHistorical(tempArr)
      }
    }
    if (data == 'RA') {
      if (POsortRAflag == 'Asc') {
        setPOsortRAflag('Desc')

        let tempArr = temp.sort((a: any, b: any) =>
          a.created_at > b.created_at ? 1 : b.created_at > a.created_at ? -1 : 0
        )
        tabSelected == 'current' ? setAllPOs(tempArr) : setAllPOHistorical(tempArr)
      } else {
        setPOsortRAflag('Asc')

        let tempArr = temp.sort((a: any, b: any) =>
          b.created_at > a.created_at ? 1 : a.created_at > b.created_at ? -1 : 0
        )
        tabSelected == 'current' ? setAllPOs(tempArr) : setAllPOHistorical(tempArr)
      }
    }
    if (data == 'SC') {
      if (POsortSCflag == 'Asc') {
        setPOsortSCflag('Desc')

        let tempArr = temp.sort((a: any, b: any) =>
          a.code > b.code ? 1 : b.code > a.code ? -1 : 0
        )
        tabSelected == 'current' ? setAllPOs(tempArr) : setAllPOHistorical(tempArr)
      } else {
        setPOsortSCflag('Asc')

        let tempArr = temp.sort((a: any, b: any) =>
          b.code > a.code ? 1 : a.code > b.code ? -1 : 0
        )
        tabSelected == 'current' ? setAllPOs(tempArr) : setAllPOHistorical(tempArr)
      }
    }
    if (data == 'STATUS') {
      if (POsortSTATUSflag == 'Asc') {
        setPOsortSTATUSflag('Desc')

        let tempArr = temp.sort((a: any, b: any) =>
          a.status > b.status ? 1 : b.status > a.status ? -1 : 0
        )
        tabSelected == 'current' ? setAllPOs(tempArr) : setAllPOHistorical(tempArr)
      } else {
        setPOsortSTATUSflag('Asc')

        let tempArr = temp.sort((a: any, b: any) =>
          b.status > a.status ? 1 : a.status > b.status ? -1 : 0
        )
        tabSelected == 'current' ? setAllPOs(tempArr) : setAllPOHistorical(tempArr)
      }
    }
  }

  const OnPoSelected=(data: any)=> {
    let temp = data
    setSpid(temp.supplier_id)

    setloadingPOItems(true)
    try {
      fetch(`${mainConfig.MAIN_ENDPOINT}/api/get-poitems/${data.po_id}`, {
        method: 'get',
        headers: {
          Authorization: `Bearer ${token}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      })
        .then(function (response) {
          if (response.status == 404) {
            setloadingPOItems(false)
            return
          }

          if (response.status != 200) {
            setloadingPOItems(false)
            return
          }
          response.json().then(function (data) {
            if (response.status == 404) {
              getAllPO()
              setloading(false)
            } else {
            }
            if (tabSelected == 'Latest') {
              setSpCode(data.data[0].supplier)
              setAllPOItem(data.data)
              setAllPOItemTable(data.data)
              setPONumberSelected(temp.po_num)
              setIscreateP(temp.po_id)
            }
            if (tabSelected == 'Historical') {
              setAllPOItemH(data.data)
              setAllPOItemTableH(data.data)
              setPONumberSelectedH(temp.po_num)
              setIscreateP(data.po_id)
            }
            setloadingPOItems(false)
          })
        })
        .catch(function (err) {
          setloading(false)
        })
    } catch (err) {
      setloading(false)
    }
  }

  const ondateSelect=(date: any)=> {
    setAllPOItem([])
    let tempData = AllPOsTable
    if (date) {
      let tempDate = moment(date[0]).format('YYYY-MM-DD')
      let tempDate1 = moment(date[1]).format('YYYY-MM-DD')
      setFilterDate(date)
      let searchdata = new Array()
      if (tabSelected == 'Latest') {
        if (tempDate != 'Invalid date') {
          tempData.map((i) => {
            if (
              moment(i.created_at).format('YYYY-MM-DD') >= tempDate &&
              moment(i.created_at).format('YYYY-MM-DD') <= tempDate1
            ) {
              searchdata.push(i)
            }
          })
          setAllPOs(searchdata)
        } else {
          setAllPOs(AllPOsTable)
        }
      }
    } else {
      setAllPOs(AllPOsTable)
    }
  }

  token = JSON.parse(localStorage.getItem('portalUserDataToken') || '')
  return (
    <React.Fragment>
      {loading == true ? (
        <React.Fragment>
          <Skeleton loading={loading} active></Skeleton>
        </React.Fragment>
      ) : (
        <React.Fragment>
          <Row>
            <Col span={20}>
              <ol className='breadcrumb breadcrumb-dot fs-6 fw-bold' style={{margin: '1%'}}>
                <Link to='/home' className='pe-1 cursor-pointer'>
                  <li className='breadcrumb-item pe-1'>Home</li>
                </Link>
                / &nbsp;<li className='breadcrumb-item pe-3'>Po Items</li>
              </ol>
            </Col>
          </Row>
          <div className='row g-5 gx-xxl-8' style={{marginTop: '1%'}}>
            <div className='col-xxl-12'>
              <div className='card-xxl-stretch mb-5 mb-xxl-8'>
                <div className='row gy-5 g-xl-4'>
                  <div className={`card`}>
                    <div className='card-header border-0 pt-2'>
                      <h3 className='card-title align-items-start flex-column'>
                        <span
                          className='card-label fw-bolder fs-3 mb-1'
                          style={{color: 'rgb(138, 38, 80)'}}
                        >
                          Recent Po's
                        </span>
                      </h3>
                      <div className='card-toolbar' style={{width: '30%'}}>
                        <input
                          type='search'
                          onChange={onSearchPO}
                          className='form-control form-control-solid'
                          placeholder='Search by PO Number / Raised By'
                        />
                      </div>
                      <div
                        className='card-toolbar'
                        data-bs-toggle='tooltip'
                        data-bs-placement='top'
                        data-bs-trigger='hover'
                        title='filter by Date'
                      >
                        {tabSelected == 'Latest' && (
                          <div
                            className='card-toolbar'
                            data-bs-toggle='tooltip'
                            data-bs-placement='top'
                            data-bs-trigger='hover'
                            title=''
                          >
                            <RangePicker onChange={(date) => ondateSelect(date)} />
                          </div>
                        )}
                      </div>
                      <div
                        className='card-toolbar'
                        data-bs-toggle='tooltip'
                        data-bs-placement='top'
                        data-bs-trigger='hover'
                        title='Click to Create New PO'
                      ></div>
                    </div>
                    <div className='card-body py-3 '>
                      <div className='tab-content' id='myTabContent'>
                        <div className='tab-pane fade show active' id='latestPO' role='tabpanel'>
                          <h3 className='card-title align-items-start flex-column'>
                            <span style={{fontWeight: 'normal'}}>
                              {' '}
                              <sub className={''} style={{color: 'black'}}>
                                (Last 3 week)
                              </sub>
                            </span>
                          </h3>
                          <Row>
                            <Col span={12}>
                              <div className='table-responsive h-600px table-scroll'>
                                <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4 main-table'>
                                  <thead>
                                    <tr className='fw-bolder text-muted'>
                                      <th
                                        className='min-w-80px'
                                        onClick={() => onSortpoTable(POsortPN)}
                                      >
                                        PO-Number{' '}
                                        {POsortPNflag != 'Desc' ? (
                                          <KTSVG
                                            className='text-hover-primary cursor-pointer'
                                            path={'/media/icons/duotune/arrows/arr062.svg'}
                                          />
                                        ) : (
                                          <KTSVG
                                            className='text-hover-primary cursor-pointer'
                                            path={'/media/icons/duotune/arrows/arr068.svg'}
                                          />
                                        )}
                                      </th>
                                      <th
                                        className='min-w-50px'
                                        onClick={() => onSortpoTable(POsortSC)}
                                      >
                                        Supplier Code
                                        {POsortSCflag != 'Desc' ? (
                                          <KTSVG
                                            className='text-hover-primary cursor-pointer'
                                            path={'/media/icons/duotune/arrows/arr062.svg'}
                                          />
                                        ) : (
                                          <KTSVG
                                            className='text-hover-primary cursor-pointer'
                                            path={'/media/icons/duotune/arrows/arr068.svg'}
                                          />
                                        )}
                                      </th>
                                      <th className='min-w-100px'>Raised By</th>

                                      <th
                                        className='min-w-80px'
                                        onClick={() => onSortpoTable(POsortRA)}
                                      >
                                        Raised At{' '}
                                        {POsortRAflag != 'Desc' ? (
                                          <KTSVG
                                            className='text-hover-primary cursor-pointer'
                                            path={'/media/icons/duotune/arrows/arr062.svg'}
                                          />
                                        ) : (
                                          <KTSVG
                                            className='text-hover-primary cursor-pointer'
                                            path={'/media/icons/duotune/arrows/arr068.svg'}
                                          />
                                        )}
                                      </th>

                                      <th
                                        className='min-w-80px'
                                        onClick={() => onSortpoTable(POsortSTATUS)}
                                      >
                                        Status{' '}
                                        {POsortSTATUSflag != 'Desc' ? (
                                          <KTSVG
                                            className='text-hover-primary cursor-pointer'
                                            path={'/media/icons/duotune/arrows/arr062.svg'}
                                          />
                                        ) : (
                                          <KTSVG
                                            className='text-hover-primary cursor-pointer'
                                            path={'/media/icons/duotune/arrows/arr068.svg'}
                                          />
                                        )}
                                      </th>
                                    </tr>
                                  </thead>
                                  <tbody>
                                    {AllPOs != undefined &&
                                      AllPOs.map((j) => {
                                        return (
                                          <tr>
                                            <td>
                                              <span
                                                className='text-danger fw-bolder text-hover-primary d-block fs-6 cursor-pointer text-decoration-underline link'
                                                onClick={() => OnPoSelected(j)}
                                              >
                                                {j.po_num}
                                              </span>
                                            </td>
                                            <td>
                                              <span className='text-danger fw-bolder text-hover-primary d-block fs-6 cursor-pointer  link'>
                                                {j.code}
                                              </span>
                                            </td>
                                            <td>
                                              <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                {j.created_by ? j.created_by : '-'}
                                              </span>
                                            </td>
                                            <td>
                                              <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                {j.created_at
                                                  ? moment(j.created_at).format('DD-MMM-YYYY')
                                                  : '-'}
                                              </span>
                                            </td>
                                            <td>
                                              <div className='d-flex flex-shrink-0'>
                                                {j.status == '0' ? (
                                                  <button
                                                    className='btn btn-text btn-color-light btn-bg-warning fw-bold btn-sm me-1 min-w-100px'
                                                    style={{cursor: 'text'}}
                                                  >
                                                    {'In Progress'}
                                                  </button>
                                                ) : j.status == '1' ? (
                                                  <button
                                                    className='btn btn-text btn-color-light btn-bg-danger fw-bold btn-sm me-1 min-w-100px'
                                                    style={{cursor: 'text'}}
                                                  >
                                                    {'Approval Pending'}
                                                  </button>
                                                ) : j.status == '2' ? (
                                                  <button
                                                    className='btn btn-text btn-color-light btn-bg-primary fw-bold btn-sm me-1 min-w-100px'
                                                    style={{cursor: 'text'}}
                                                  >
                                                    {'Approved'}
                                                  </button>
                                                ) : j.status == '3' ? (
                                                  <button
                                                    className='btn btn-text btn-color-light btn-bg-success fw-bold btn-sm me-1 min-w-100px'
                                                    style={{cursor: 'text'}}
                                                  >
                                                    {'Complete'}
                                                  </button>
                                                ) : j.status == '4' ? (
                                                  <button
                                                    className='btn btn-text btn-color-light btn-bg-danger fw-bold btn-sm me-1 min-w-100px'
                                                    style={{cursor: 'text'}}
                                                  >
                                                    {'Expired'}
                                                  </button>
                                                ) : (
                                                  '-'
                                                )}
                                              </div>
                                            </td>
                                          </tr>
                                        )
                                      })}
                                  </tbody>
                                </table>
                              </div>
                            </Col>
                            <Col span={1}></Col>
                            <Col span={11}>
                              <div>
                                {loadingPOItems ? (
                                  <div style={{margin: '25% 50% 50% 50%'}}>
                                    <Spin
                                      tip='Loading...'
                                      size='large'
                                      style={{justifyContent: 'center'}}
                                    />
                                  </div>
                                ) : (
                                  <React.Fragment>
                                    <div className='table-responsive  h-600px table-scroll'>
                                      <table className='table table-row-dashed table-row-gray-300 align-middle gs-0 gy-4 main-table'>
                                        {AllPOItem.length > 0 && (
                                          <thead>
                                            <tr className='fw-bolder text-muted'>
                                              <th className='w-20px'>
                                                <Button
                                                  type='dashed'
                                                  style={{
                                                    color: 'rgb(138, 38, 80)',
                                                    cursor: 'text',
                                                  }}
                                                >
                                                  {PONumberSelected}
                                                </Button>
                                              </th>

                                              <th className='min-w-100px'></th>
                                              <th className='min-w-70px cursor-pointer'></th>
                                            </tr>
                                          </thead>
                                        )}
                                        <thead>
                                          <tr className='fw-bolder text-muted'>
                                            <th className='min-w-100px'>Supplier</th>
                                            <th className='min-w-100px'>Product Code</th>
                                            <th className='min-w-100px'>Product Description</th>
                                            <th className='min-w-70px cursor-pointer'>Quantity</th>
                                            <th className='min-w-70px cursor-pointer'>Price</th>
                                          </tr>
                                        </thead>
                                        <tbody>
                                          {AllPOItem != undefined &&
                                            AllPOItem.map((j) => {
                                             
                                              return (
                                                <tr>
                                                  <td>
                                                    <Link
                                                      to={{
                                                        pathname: '/supplier',
                                                        state: {id: j.supplier_id},
                                                      }}
                                                    >
                                                      <span className='text-danger fw-bolder text-hover-primary d-block fs-6 cursor-pointer text-decoration-underline link'>
                                                        {j.supplier}
                                                      </span>
                                                    </Link>
                                                  </td>
                                                  <td>
                                            <Link
                                              to={{
                                                pathname: '/product',
                                                state: {
                                                  prod_id: j.prod_id,
                                                },
                                              }}
                                            >
                                              <span className='text-danger fw-bolder text-hover-primary d-block fs-6 text-decoration-underline link'>
                                                {j.product_code ? j.product_code : '-'}
                                              </span>
                                            </Link>
                                          </td>
                                                  <td>
                                                  <ShowMore
                                                    lines={1}
                                                    more='Show more'
                                                    less='Show less'
                                                    anchorClass=''
                                                  >
                                                    {j.desc ? j.desc : '-'}
                                                  </ShowMore>
                                                </td>
                                                  <td>
                                                    <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                      {j.quantity}
                                                    </span>
                                                  </td>
                                                  <td>
                                                    <span className='text-dark fw-bolder text-hover-primary d-block fs-6'>
                                                      {j.price.charAt(0) == '.'
                                                        ? '0' + j.price
                                                        : j.price}
                                                    </span>
                                                  </td>
                                                </tr>
                                              )
                                            })}
                                        </tbody>
                                      </table>
                                    </div>
                                  </React.Fragment>
                                )}
                              </div>
                            </Col>
                          </Row>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default withRouter(RecentPo)
